/* breadcrumb */ 

.breadcrumb {
  list-style: none;
  margin-bottom: 2rem;
}

.breadcrumb li:first-child {
  padding-left: 0;
}

.breadcrumb li {
  display: inline-block;
  padding: .5em;
  position: relative;
  text-decoration: none;
  white-space: nowrap;
  line-height: 1.5em;
}

.breadcrumb li+li:before {
  @include pseudo($display: inline-block, $pos: relative, $content: "\f105");
  margin-right: 1em;
  font-family: fontello;
  line-height: 1.5em;
}
